.center-left{
	padding: 30px 0;
	margin: 0px auto;
	height: 860px;
	background-color:white ;
	border-top: 1px solid #add7ea;
	.center-left-head{
		margin: 0 20px;
	}
}
.center-left-head p{
	letter-spacing: 5px;
	text-align: center;
	font-size: 24px;
	font-weight: bold;
}
.center-left-head .note{
	margin-top: 4px;
	padding:0 196px 15px 196px;
	border-bottom: solid 1px #CCCCCC;
	height: 20px;
}
.center-left-head span{
	font-size: 14px;
	font-weight: bold;
	color:#666666 ;
}
.center-left-img{
	margin-top: 20px;
	text-align: center;
}
.center-left-head .llcs{
	margin-left: 30px;
	
}
.center-left-head .llcs-cs{
	margin-left: 0px;
	
}
.center-left-news{
	margin: 25px;
}
.center-left-news p{
	letter-spacing: 2px;
	text-indent: 2em;
	font-size: 16px;
	color: #333333;
	text-align: left;
	line-height: 30px;
}
