.cyq-wrap{
	width: 748px;
	height: 966px;
	font-family: "微软雅黑";
	color: #707070;
	background-color: #fff;
	min-height: 966px;
	.head{
		height: 45px;
		border-bottom: 1px solid #AED7EA;
		font-size: 14px;
		background-color: #fff;
		.head-title{
			height: 45px;
    		line-height: 45px;
			i{
				width: 3px;
			    height: 14px;
			    background-color: #dd3508;
			    display: inline-block;
			    margin-left: 11px;
			    margin-right: 10px;
			    vertical-align: sub;
			}
			span{
				line-height: 45px;
			    color: #666;
			    font-size: 14px;
			}
			.span-address{
				cursor: pointer;
			    margin-right: 10px;
			    font-weight: bold;
			}
			.active{
				color: #0c2a48;
			}
		}
	}
    .serch-box{
    	height: 50px;
    	background-color: #E9F4F9;
    	border-bottom: 1px solid #AED7EA; 
    }
	.content{
		background-color: #fff;
		.con-title{
			font-size: 18px;
			margin-top: 24px;
			text-align: center;
			color: #6A6A6A;
			font-weight: bolder;
		}
		.titles{
			padding: 0 30px;
			font-size: 14px;
			ul{
				li{
					h2{
					margin-top:17px;
					}
					.tit-first{
						color: #666666;
						font-weight: bolder;
					}
					p{
						line-height: 23px;
					}
				}
			}
			
		}
	}
}