.ai-wrapper-lf{
	border:1px solid #c0c0c0;
	background-color: #fff;
}

.ai-mian{
	padding:20px;
	border-top: 1px solid #add7ea;
	.tips{
		font-size: 12px;
		color: #666;
		span{
			color: #ec433d;
		}
	}
	.content{
		table{
			width: 100%;
			font-size: 14px;
			color: #666;
			td{
				height: 50px;
				line-height: 50px;
				img{
					display: inline-block;
					vertical-align: middle;
				}

				.prompt{
					overflow: hidden;
					font-size: 12px;
					color: #ec433d;
					b{
						height: 100%;
						float: left;
						font-weight: normal;
						line-height: 20px;
					}
					p{
						line-height: 20px;
						margin-left: 25px;
					}
				}
				span{
					display: inline-block;
					vertical-align: middle;
					min-width: 82px;
					&.vt{
						height: 30px;
						line-height: 30px;
						vertical-align: top;
					}
					&.change{
						color: #1680f6;
						font-size: 14px;
						margin-left: 20px;
						cursor: pointer;
					}
				}
			}
			input{
				display: inline-block;
				width: 230px;
				padding: 0 10px;
				height: 25px;
				vertical-align: middle;
				border: 1px solid #dbd9d9;
				&:active{
					outline:none;
				}
				&.long{
					width: 585px;
				}
				&.short{
					width: 147px;
				}

			}
			textarea{
				width: 585px;
				padding: 10px;
				height: 298px;
				border: 1px solid #dbd9d9;
			}
			select{
				width: 250px;
				padding: 0 10px;
				border: 1px solid #dbd9d9;
				background: none;
				height: 27px;
				color: #666;
			}
			i{
				font-style: normal;
				color: #db1c1b;
				width: 10px;
				display: inline-block;
			}
		}
		.btnList{
			text-align: center;
			margin: 50px 0;
			span{
				display: inline-block;
				width: 135px;
				height: 46px;
				line-height: 46px;
				text-align: center;
				font-size: 20px;
				color: #fff;
				margin: 0 10px;
				cursor: pointer;
				&.save{
					background: #1680f6;
				}
				&.commit{
					background: #dd3508;
				}
			}
		}
	}
}
.ai-mode{
	height:30px;
	line-height: 30px;
	margin-bottom: 15px;
}

.ai-mode-lf{
	font-size: 14px;
	color: #666;
	float: left;
	span{
		font-size: 20px;
		color: #d71e1d;
		margin-left: 10px;
		font-weight: bold;
	}
}

.ai-mode-rg{
	font-size: 14px;
	color: #666;
	float: right;
	span{
		margin-left: 7px;
	}
}

.ai-form{
	width: 100%;
	table{
		width: 100%;
		border: 1px solid #e0e0e0;
		tr{
			height: 48px;
			line-height: 48px;
			td{
				font-size: 14px;
				color: #666;
				text-indent: 15px;
				height: 100%;
				border: 1px solid #e0e0e0;
				i{
					display: inline-block;
					width: 7px;
					height: 20px;
				}
				em{
					color: #d71e1d;
					font-size: 14px;
				}
			}
		}
	}
}

.ai-form table tr .ai-back{
	color: #333333;
	text-align: center;
	background-color: #f7f7f7;
	text-indent: 0;
}

.ai-heig-td{
	height: 160px;
	line-height: 25px;
	.ai-p-mg{
		margin-left: 15px;
	}
}

.radio_label{
	background: url(../img/ai-radio_h.png) no-repeat;
	display: inline-block;
	*display: inline;
	*zoom: 1;
    text-indent: 20px;
	height: 14px;
	line-height: 14px;
	vertical-align: middle;
    margin-right: 20px;
    vertical-align: middle;
}

.ai-checked{
	background: url(../img/ai-radio_l.png) no-repeat;
}

.ai-input{
	display:none;
	position: absolute;
    left: -999px;
}

.ai-pj-widt{
	height: 75px;
}


/*面包屑*/
.ai-location{
	height: 45px;
	line-height: 45px;
	font-size: 14px;
	color: #666;
	i{
		width: 3px;
		height: 15px;
		font-style: normal;
		background-color: #dd3508;
		display: inline-block;
		vertical-align: sub;
		margin-right: 10px;
		margin-left: 10px;
	}
	a{
		color: #666;
		text-decoration: none;
	}
	a:hover{
		text-decoration: none;
	}
	.ai-modern{
		color: #0c2a48;
	    font-weight: bold;
	}
}

/*框架页*/
.ai-mian-center{
	margin-top: 20px;
}
.ai-main-lf{
	float: left;
	width: 750px;
	background-color: #fff;
	border:1px solid #c0c0c0;
}

.ai-main-rg{
	float: right;
	width: 240px;
	border:1px solid #c0c0c0;
	background-color: #fff;
	height: 966px;
	ul{
		padding: 5px;
		li{
			margin-bottom: 5px;
		}
		h3{
			font-size: 24px;
			color: #666666;
		}
		p{
			font-size: 12px;
			color: #888888;
			text-align: center;
		}
	}
}

