			}
			.xian-name{
				line-height: 70px;
				color: #666;
				font-size: 24px;
				margin: 0 15px;
			}
		}
	}
	.server-right-con{
		background-color: #fff;
		width: 749px;
		height: 966px;
		border:1px solid #c0c0c0;
		margin-left: 251px;
		.cont-table{

		}
		table{
			width: 100%;
			.table-title{
				background-color: #e9f4f9;
			}
			.number{
				width: 55px;
			}
			th{
				color: #666;
				font-size: 12px;
				text-align: center;
				height: 48px;
				line-height: 48px;
				border-bottom: 1px solid #add7ea;
				border-top: 1px solid #add7ea;
				border-right: 1px solid #dfdfdf;
			}
			td{
				height: 49px;
				line-height: 49px;
				color: #444;
				font-size: 14px;
				text-align: center;
				border-right: 1px solid #dfdfdf;
				border-bottom: 1px solid #dfdfdf;
			}
		}
	}
}
/*分页*/
.list-page {
    margin:22px 0;
    text-align: center;
    font-size: 0;
}
.list-page a.pre, .list-page a.next{
    font-size: 14px;
    border-color: #e0e0e0;
    color: #555;
    padding: 3px 15px;
    border:1px #cacaca solid;
    border-radius: 5px;
    &:hover{
		border:1px #0d99e9 solid;
		color: #0d99e9;
		text-decoration:none;
    }
    &.active{
		border:1px #0d99e9 solid;
		color: #0d99e9;
		text-decoration:none;
    }
}
.list-page a {
    margin: 0 20px 0 0;
    padding: 3px;
    min-width: 25px;
    min-height: 25px;
    cursor: pointer;
    margin: 0px 6px 0 0;
}
.list-page .num.active, .list-page .num:hover {
    background: #008ef0;
    border-color: #008ef0;
    text-decoration: none;
    color: #fff;
    border-radius: 50%;
	text-decoration:none;
}
.list-page a, .list-page span {
    display: inline-block;
    zoom: 1;
    height: 25px;
    line-height: 25px;
    text-align: center;
    vertical-align: top;
    font-size: 14px;
    color: #555;
}
.list-page span.ellipsis {
    margin: 0 10px 0 5px;
}
.list-page span {
    padding: 4px 0;
}
.list-page .pagenum{
	width: 37px;
    border: 1px solid #cacaca;
    height: 26px;
    outline: none;
    color: #555;
    background-color: #fff;
    font-size: 14px;
    text-align: center;
   	padding: 0 8px;
}
.list-page span.goto-text.indent {
    padding-left: 5px;
    padding-right: 5px;
}
.list-page span.goto-input input {
    border: none;
    width: 32px;
    height: 100%;
    margin-top: -4px;
    text-align: center;
    outline: none;
    padding-left: 0;
}
.list-page span.goto-text {
    margin: 0 4px 0 0;
}
.list-page span.goto-input {
    margin: 0 7px 0 0;
    padding: 3px 0;
    border: #e0e0e0 1px solid;
}
.list-page a.goto-btn {
    margin: 0;
    min-width: 62px;
    background-color: #f1f6fa;
    color: #3395da;
    border: 1px solid #cacaca;
    border-radius: 5px;
    text-decoration:none;
}
