.faq-wrap {
    width: 750px;
    height: 966px;
    .faq-found {
        height: 50px;
        background-color: #e9f4f9;
        border-top: 1px solid #add7ea;
        border-bottom: 1px solid #add7ea;
        .found-ib {
            float: right;
            height: 32px;
            line-height: 32px;
            margin-right: 21px;
            margin-top: 9px;
            font-size: 14px;
            .found-span {
                color: #e87333;
            }
            .found-input {
                height: 30px;
                border: 1px solid #ccc;
                text-indent: 10px;
                outline: none;
                margin-right: 15px;
            }
            .found-button {
                border: 1px solid #ccc;
                border: 1px solid #ccc;
                outline: none;
                color: #3395da;
                padding: 4px 21px;
                height: 32px;
                border-radius: 3px;
                background-color: #eff5f9;
                cursor: pointer;
                font-size: 14px;
            }
        }
    }
    .faq-list {
        padding: 0 16px 0 10px;
        .faq-ul {
            font-size: 16px;
            .faq-li {
                border-bottom: 1px solid #dfdfdf;
                padding-left: 13px;
                .faq-que {
                    background: url('../img/Q.png') no-repeat;
                    height: 20px;
                    line-height: 20px;
                    text-indent: 30px;
                    margin: 16px 0;
                    color: #666;
                }
                .faq-a {
                    display: inline-block;
                    width: 30px;
                    height: 20px;
                    background: url('../img/A.png') no-repeat;
                    vertical-align: text-bottom;
                }
                .faq-ans {
                    float: right;
                    width: 681px;
                    color: #888;
                    margin-bottom: 11px;
                    line-height: 18px;
                }
            }
        }
    }
}