.wrap {
    position: relative;
    background: #eff3f5;
    em {
        font-size: 24px;
        &.blue {
            color: #127fc4;
        }
        &.gre {
            color: #0dae4d;
        }
        &.red {
            color: #f64c1f;
        }
        &.ori {
            color: #e99a01;
        }
    }
    .topBg {
        background: url(../img/topBg.png) no-repeat;
        height: 581px;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        z-index: 9;
    }
    .botBg {
        background: url(../img/botBg.png) no-repeat;
        height: 227px;
        left: 0;
        right: 0;
        bottom: -40px;
        z-index: 8;
        position: absolute;
    }
}
#loading{
    background: url(../img/loading.gif) ;
    width: 32px;
    height: 32px;
    left: 50%;
    top: 50%;
    margin-top: -16px;
    margin-left: -16px;
    position: absolute;
}   
.mainCon {
    width: 1000px;
    margin: 0 auto;
    position: relative;
    z-index: 10;
}

.footer {
    text-align: center;
    font-size: 18px;
    color: #666;
    position: relative;
    z-index: 9;
    padding-bottom: 60px;
    margin-top: 30px;
    p {
        padding: 10px 0;
    }
    i {
        background: url(../img/copyRicon.png) no-repeat;
        width: 56px;
        height: 68px;
        display: inline-block;
    }
}

.header {
    font-size: 0;
    .logo {
        background: url(../img/logo.png) no-repeat;
        width: 630px;
        height: 119px;
        display: inline-block;
    }
    .rightImg {
        background: url(../img/rightImg.png) no-repeat;
        width: 365px;
        height: 148px;
        display: inline-block;
    }
}

.mainNav {
    background: #dd3508;
    border-bottom: 2px solid #992202;
    height: 52px;
    position: relative;
    ul {
        font-size: 0;
        height: 40px;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        line-height: 36px;
        li {
            display: inline-block;
            font-size: 16px;
            color: #fff;
            width: 14.2%;
            text-align: center;
            position: relative;
            i {
                width: 1px;
                height: 20px;
                background: #f15f38;
                position: absolute;
                right: 0px;
                top: 10px;
            }
            span {
                display: inline-block;
                width: 109px;
                height: 36px;
                border: 2px solid #dd3508;
                vertical-align: bottom;
            }
            &.active {
                span {
                    background: #c12900;
                    border-radius: 4px 4px 0 0;
                    border: 2px solid #b72700;
                }
            }
        }
    }
}

.top {
    height: 35px;
    background: #0c2a48;
    line-height: 35px;
    font-size: 14px;
    color: #fff;
    padding: 0 53px;
    overflow: hidden;
    position: relative;
    margin-bottom: 45px;
    z-index: 10;
    .welc {
        background: url(../img/topIcon.png) no-repeat;
        width: 264px;
        height: 34px;
        display: inline-block;
        text-align: center;
    }
    .date {
        display: inline-block;
    }
    .topNav {
        float: right;
        font-size: 0;
        li {
            display: inline-block;
            font-size: 14px;
            margin: 0 10px;
            span {
                cursor: pointer;
            }
        }
    }
}

.checkBtn {
    background: url(../img/checkBtnN.png) no-repeat;
    width: 70px;
    height: 40px;
    display: inline-block;
    line-height: 40px;
    font-size: 16px;
    text-align: center;
    color: #3395da;
    margin-left: 10px;
    vertical-align: top;
    cursor: pointer;
    &:hover {
        background: url(../img/checkBtn.png) no-repeat;
        color: #067bcd;
    }
}

.content {
    margin-top: 20px;
    .topCon {
        height: 300px;
        overflow: hidden;
        .bjtj {
            float: left;
            border: 1px solid #c0c0c0;
            width: 238px;
            height: 296px;
            background: #ffffff;
            ul {
                margin: 0 18px;
                overflow: hidden;
                background: #fff;
                li {
                    line-height: 55px;
                    height: 55px;
                    text-align: center;
                    font-size: 14px;
                    color: #666;
                    border-top: 1px solid #c0c0c0;
                    margin-top: -1px;
                    em {
                        margin: 0 3px;
                    }
                }
            }
        }
        .sqcx {
            float: left;
            position: relative;
            height: 298px;
            border: 1px solid #c0c0c0;
            .txsq {
                background: url(../img/sqtxnormal.png) no-repeat;
                width: 340px;
                height: 82px;
                position: absolute;
                bottom: 0;
                z-index: 999;
                cursor: pointer;
            }
            .txsq:hover {
                background: url(../img/sqtxhover.png) no-repeat;
            }
            .search {
                margin: 15px;
                input {
                    width: 218px;
                    height: 37px;
                    line-height: 37px;
                    border: 1px solid #c0c0c0;
                    outline: none;
                    display: inline-block;
                    vertical-align: top;
                    margin-bottom: 20px;
                    text-indent: 1em;
                }
                .checkBtn {
                    background: url(../img/checkBtnN.png) no-repeat;
                    width: 70px;
                    height: 40px;
                    display: inline-block;
                    line-height: 40px;
                    font-size: 16px;
                    text-align: center;
                    color: #3395da;
                    margin-left: 10px;
                    vertical-align: top;
                    cursor: pointer;
                    &:hover {
                        background: url(../img/checkBtn.png) no-repeat;
                        color: #067bcd;
                    }
                }
                p {
                    font-size: 14px;
                    color: #666;
                    padding: 5px 10px;
                    span {
                        color: #f35126;
                    }
                }
            }
        }
    }
    .centerCon {
        margin-top: 10px;
        overflow: hidden;
        .gzdt {
            background: #fff;
            float: left;
            width: 402px;
            border: 1px solid #c0c0c0;
            font-size: 0;
            .title {
                background: url(../img/gzdtIcon.png);
                width: 402px;
                height: 73px;
                display: inline-block;
                line-height: 65px;
                overflow: hidden;
                .more {
                    float: right;
                    font-size: 14px;
                    color: #fff;
                    margin-right: 20px;
                    cursor: pointer;
                }
            }
            .dtCon {
                ul {
                    margin: 0 23px;
                    font-size: 16px;
                    color: #444;
                    li {
                        overflow: hidden;
                    }
                    .date {
                        float: left;
                        width: 109px;
                        height: 83px;
                        line-height: 83px;
                        border-right: 1px solid #f3643e;
                        position: relative;
                        i {
                            position: absolute;
                            width: 10px;
                            height: 10px;
                            background: #d33106;
                            border-radius: 10px;
                            top: 50%;
                            margin-top: -9px;
                            right: -9px;
                            border: 4px solid #fff;
                            z-index: 999;
                        }
                    }
                    .proj {
                        margin-left: 135px;
                        padding: 17px 0;
                    }
                }
            }
        }
    }
    .norList {
        float: left;
        margin-left: 10px;
        margin-bottom: 10px;
        padding: 10px;
        background: #fff;
        border: 1px solid #c0c0c0;
        width: 564px;
        .title {
            font-size: 0;
            border-bottom: 1px solid #8d8d8d;
            padding-bottom: 5px;
            .sqlb {
                background: url(../img/sqlbIcon.png);
                width: 506px;
                height: 33px;
                display: inline-block;
                vertical-align: middle;
            }
            .yxlb {
                background: url(../img/yxlbIcon.png);
                width: 506px;
                height: 30px;
                display: inline-block;
                vertical-align: middle;
            }
            .cjwt {
                background: url(../img/cjwtIcon.png);
                width: 506px;
                height: 51px;
                display: inline-block;
                vertical-align: middle;
            }
            .more {
                display: inline-block;
                font-size: 14px;
                color: #0c2a48;
                vertical-align: middle;
                margin-left:5px;
                cursor: pointer;
            }
        }
        .question {
            font-size: 0;
            overflow: hidden;
            li {
                font-size: 16px;
                color: #666;
                border-top: 1px solid #dfdfdf;
                margin-top: -1px;
                p {
                    height: 40px;
                    line-height: 40px;
                    i {
                        display: inline-block;
                        vertical-align: -3px;
                        margin-right: 8px;
                        &.qTip {
                            background: url(../img/Qicon.png) no-repeat;
                            width: 16px;
                            height: 20px;
                        }
                        &.aTip {
                            background: url(../img/Aicon.png) no-repeat;
                            width: 15px;
                            height: 16px;
                        }
                    }
                }
            }
        }
        table {
            width: 100%;
            th {
                font-size: 12px;
                color: #666;
                height: 32px;
                line-height: 32px;
                text-align: center;
            }
            td {
                font-size: 14px;
                color: #444;
                border-top: 1px solid #dfdfdf;
                height: 47px;
                line-height: 47px;
                text-align: center;
                .ori {
                    color: #2b9107;
                }
            }
        }
    }
    .botCon {
        overflow: hidden;
        .contact {
            background: #fff url(../img/contactBg.png) no-repeat;
            width: 404px;
            height: 236px;
            float: left;
            ul {
                margin: 10px;
                overflow: hidden;
                li {
                    font-size: 18px;
                    color: #666;
                    height: 70px;
                    line-height: 70px;
                    border-top: 1px solid #c0c0c0;
                    margin-top: -1px;
                    i {
                        font-family: 'reducto_condensed_ssiCn';
                        font-size: 30px;
                        font-weight: bold;
                        color: #0c2a48;
                        &.mail {
                            font-size: 18px;
                            font-style: normal;
                            font-family: "微软雅黑";
                        }
                    }
                }
            }
        }
    }
}

.focusBox {
    position: relative;
    float: left;
    width: 400px;
    height: 298px;
    overflow: hidden;
    font: 12px/1.5 Verdana, Geneva, sans-serif;
    text-align: left;
    background: white;
    margin: 0 10px;
}

.focusBox .pic img {
    width: 400px;
    height: 298px;
    display: block;
}

.focusBox .txt-bg {
    position: absolute;
    bottom: 0;
    z-index: 1;
    height: 36px;
    width: 100%;
    background: #333;
    filter: alpha(opacity=40);
    opacity: 0.4;
    overflow: hidden;
}

.focusBox .txt {
    position: absolute;
    bottom: 0;
    z-index: 2;
    height: 36px;
    width: 100%;
    overflow: hidden;
}

.focusBox .txt li {
    height: 36px;
    line-height: 36px;
    position: absolute;
    bottom: -36px;
}

.focusBox .txt li a {
    display: block;
    color: white;
    padding: 0 0 0 10px;
    font-size: 12px;
    font-weight: bold;
    text-decoration: none;
}

.focusBox .num {
    position: absolute;
    z-index: 3;
    bottom: 8px;
    right: 8px;
}

.focusBox .num li {
    float: left;
    position: relative;
    width: 18px;
    height: 15px;
    line-height: 15px;
    overflow: hidden;
    text-align: center;
    margin-right: 1px;
    cursor: pointer;
}

.focusBox .num li a,
.focusBox .num li span {
    position: absolute;
    z-index: 2;
    display: block;
    color: white;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    text-decoration: none;
}

.focusBox .num li span {
    z-index: 1;
    background: black;
    filter: alpha(opacity=50);
    opacity: 0.5;
}

.focusBox .num li.on a,
.focusBox .num a:hover {
    background: #f60;
}