.qt-wrap {
	margin: 0 auto;
	.qt-search {
		width: 100%;
		height: 48px;
		border-top: 1px solid #add7ea;
		border-bottom: 1px solid #add7ea;
		background-color: #e9f4f9;
	}
	.qt-keywords,
	.qt-recordtime {
		height: 50px;
		float: left;
	}
	.qt-keywords {
		width: 226px;
		margin-left: 13px;
		span {
			float: left;
			font-size: 14px;
			color: #e87333;
			width: 46px;
			line-height: 50px;
			line-height: 50px;
		}
		input {
			margin-top: 10px;
			float: left;
			outline: none;
			height: 28px;
			line-height: 28px;
			width: 178px;
			border: 1px solid #c0c0c0;
		}
	}
	.qt-recordtime {
		margin-left: 43px;
		span,
		.qt-timeshart,
		.qt-timeend {
			float: left;
		}
		span {
			height: 50px;
			font-size: 14px;
			color: #e87333;
			line-height: 50px;
		}
		.qt-timeshart,
		.qt-timeend {
			height: 28px;
			width: 118px;
			border: 1px solid #c0c0c0;
			background: #fff;
			input {
				float: left;
				outline: none;
				border: none;
				height: 28px;
				line-height: 28px;
				width: 86px;
			}
			i {
				float: right;
				margin-top: 6px;
				height: 16px;
				width: 24px;
				border-left: 1px solid #dadada;
			}
		}
		.qt-timeshart i {
			background: url(../img/qt-calender02.png) no-repeat center top;
		}
		.qt-timeend i {
			background: url(../img/qt-calender01.png) no-repeat center top;
		}
		.qt-timeshart {
			margin: 10px 6px 0 4px;
		}
		.qt-timeend {
			margin: 10px 52px 0 7px;
		}
	}
	.qt-searchBtn {
		float: left;
		margin-top: 10px;
		font-size: 14px;
		color: #3395da;
		height: 28px;
		line-height: 28px;
		text-align: center;
		width: 68px;
		border: 1px solid #bcc8d1;
		background: #f0f5f9;
		border-radius: 3px;
	}
	.qt-table {
		width: 100%;
		table {
			width: 98%;
			border: none;
			margin: 0 2px 0 7px;
			thead {
				height: 33px;
				width: 98%;
				tr {
					height: 32px;
					width: 98%;
				}
				th {
					font-size: 12px;
					color: #666;
					border-bottom: 1px solid #dfdfdf;
					height: 33px;
					line-height: 33px;
					text-align: center;
				}
				.qt-textleft {
					text-align: left;
				}
			}
			tbody {
				width: 100%;
				tr {
					font-size: 14px;
					color: #444444;
					border-bottom: 1px solid #dfdfdf;
					td {
						height: 50px;
						line-height: 50px;
						text-align: center;
					}
				}
				.qt-textleft {
					text-align: left;
				}
				.qt-green {
					color: #2b9107;
				}
				.qt-spantimestart,
				.qt-spantimeend {
					float: left;
					margin: 0 10px;
				}
			}
			.qt-table-serial {
				width: 5%;
			}
			.qt-table-type {
				width: 10%;
			}
			.qt-table-appealtime {
				width: 26%;
			}
			.qt-table-endtime {
				width: 26%;
			}
			.qt-table-title {
				width: 24%;
			}
			.qt-table-operation {
				width: 9%;
			}
			.qt-mar {
				padding-left: 5%;
			}
		}
	}
}